#hero {
  width: 100%;
  background: #0f1c4d;
  background: linear-gradient(#080808, #000339ba, #080808), url("../../.././img/retro-bg.jpg");
  background-position: center;
  background-size: 15%;
  padding-top: 56px;
  padding-top: 56px;

  .hero-bg {
    margin: 0 auto;
    max-width: 1400px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    position: relative;

    .rbp-img {
      display: flex;
      justify-content: center;
      align-items: center;
      z-index: 1;

      img {
        width: 100%;
        max-width: 480px;
      }
    }

    .rbp-img2 {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      flex-grow: 2;
      z-index: 1;

      h2 {
        font-size: 2.5rem;
        color: white;
        font-weight: 700;
        letter-spacing: 0.2rem;
        text-align: center;
        margin-right: 5%;

        span {
          font-size: 3.5rem;
          letter-spacing: 0;
        }
      }
    }
    .vid-wrapper{
      position: absolute;
      width: 32%;
      height: 100%;
      z-index: 0;
      opacity: 0;
      overflow: hidden;
      animation: fade-in 1s ease-in 2s forwards;
          .vid{
            width: 190%;
            left: -50%;
            position: relative;
          }
    }
  }
}


@media screen and (max-width: 1199px) {
  #hero{
    .hero-bg{
      justify-content: center;
      .rbp-img{
        width: 100%;
      }
      .rbp-img2{
        justify-content: center;
            margin: 0 20px;
        h2{
          font-size: 2rem;
          text-align: center;
          margin: 0 0 50px;
          margin-right: 0%;
        }
      }
    }
  }
}

@media screen and (max-width:  767px) {
  #hero{
    min-height: calc(100vh);
  display: flex;
    .hero-bg{
      .rbp-img{
      }
      .rbp-img2{
        h2{
          font-size: 1.5rem;
          span{
              font-size: 2.5rem;
          }
        }
      }
    }
  }
}
@keyframes fade-in{
  0%{
    opacity: 0;
  }
  100%{
    opacity: .3;
  }
}
